import 'antd/dist/antd.css'
import 'moment/locale/zh-cn'
import { Button, Table } from 'antd'
import { useState } from 'react'
import Axios from 'axios'
import React from 'react'
function Load () {
    const { Column } = Table
    const [data, setData] = useState()
    const getItems = () => {
        Axios.get(
            'http://localhost:3001/items')
            .then((response) => {
                setData(response.data)
                console.log(response)
            })
    }
    return (
        <>
            <Button onClick={getItems}></Button>
            <Table dataSource={data}>
                <Column title="user" dataIndex="user" key="user" />
                <Column title="desc" dataIndex="desc" key="desc" />
                <Column title="info" dataIndex="info" key="info" />
                <Column title="image" dataIndex="image" key="image" />
                <Column title="education" dataIndex="education" key="education" />
                <Column title="experience" dataIndex="experience" key="experience" />
                <Column title="skill" dataIndex="skill" key="skill" />
            </Table>
        </>
    )
}

export default Load