import 'antd/dist/antd.css'
import 'moment/locale/zh-cn'
import { useNavigate } from "react-router-dom"
import { Input, Form, Button } from 'antd'
import resume from "../../resume.data"
import Axios from 'axios'
import { useState } from 'react'
const { TextArea } = Input
const layout = {
    labelCol: { span: 2 },
    wrapperCol: { span: 15 },
}
function Edit () {
    const navigate = useNavigate()
    var data = JSON.parse(
        JSON.stringify(resume)
    )
    const [user, setUser] = useState(JSON.stringify(data.user))
    const [desc, setDes] = useState(JSON.stringify(data.desc))
    const [image, setImage] = useState(JSON.stringify(data.image))
    const [info, setInfo] = useState(JSON.stringify(data.info))
    const [education, setEducation] = useState(JSON.stringify(data.education))
    const [experience, setExperience] = useState(JSON.stringify(data.experience))
    const [skill, setSkill] = useState(JSON.stringify(data.skill))
    const onFinish = (values) => {
        Axios.post(
            'http://localhost:3001/create',
            {
                user: values.user, desc: values.desc, image: values.image, info: values.info,
                education: values.education, experience: values.experience, skill: values.skill
            })
        data = {
            user: JSON.parse(values.user), desc: JSON.parse(values.desc), image: JSON.parse(values.image), info: JSON.parse(values.info),
            education: JSON.parse(values.education), experience: JSON.parse(values.experience), skill: JSON.parse(values.skill)
        }
    }


    const load = () => {
        navigate('/Show', { state: { data: data } })
    }

    return (
        <Form {...layout} name="resume" onFinish={onFinish} initialValues={{
            user: user, desc: desc, image: image, info: info,
            education: education, experience: experience, skill: skill
        }}>
            <Form.Item name={'user'} label="Name" rules={[{ required: true }]}>
                <TextArea
                    value={user}
                    onChange={(e) => setUser(e.target.user)}
                    autoSize={true}
                />
            </Form.Item>
            <Form.Item name={'desc'} label="Desc" >
                <TextArea
                    value={desc}
                    onChange={(e) => setDes(e.target.des)}
                    autoSize={true}
                />
            </Form.Item>
            <Form.Item name={'image'} label="Image">
                <TextArea
                    value={image}
                    onChange={(e) => setImage(e.target.image)}
                    autoSize={true}
                />
            </Form.Item>
            <Form.Item name={'info'} label="Info">
                <TextArea
                    value={info}
                    onChange={(e) => setInfo(e.target.info)}
                    autoSize={true}
                />
            </Form.Item>
            <Form.Item name={'education'} label="Education">
                <TextArea
                    value={education}
                    onChange={(e) => setEducation(e.target.education)}
                    autoSize={true}
                />
            </Form.Item>
            <Form.Item name={'experience'} label="Experience">
                <TextArea
                    value={experience}
                    onChange={(e) => setExperience(e.target.experience)}
                    autoSize={true}
                />
            </Form.Item>
            <Form.Item name={'skill'} label="Skill">
                <TextArea
                    value={skill}
                    onChange={(e) => setSkill(e.target.skill)}
                    autoSize={true}
                />
            </Form.Item>
            <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
                <Button type="primary" htmlType="submit" >
                    
                </Button>
                <Button type="dashed" onClick={load} style={{ margin: '0 16px' }}>
                    
                </Button>
            </Form.Item>
        </Form >
    )
}

export default Edit